<template>
  <div id="about" class="about-me-container" ref="aboutSection">
    <h1 class="about-me-title">Über mich</h1>
    <div class="about-me">
      <div class="profile-container">
        <img src="@/assets/Elyas.jpg" alt="Elyas" class="profile-img" />
      </div>
      <p class="description">{{ description }}</p>
      
      <div class="contact-info">
        <div class="contact-item">
          <i class="fas fa-envelope"></i>
          <a :href="'mailto:' + email">{{ email }}</a>
        </div>
        <div class="contact-item">
          <i class="fab fa-whatsapp"></i>
          <i class="fas fa-phone"></i>
          <a :href="'tel:' + phone">{{ phone }}</a>
        </div>
      </div>

      <div class="social-links">
        <a :href="github" class="social-link github">
          <i class="fab fa-github"></i> GitHub
        </a>
        <a :href="linkedin" class="social-link linkedin">
          <i class="fab fa-linkedin"></i> LinkedIn
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      description: `Mein Name ist Elyas Jabari, ich bin 22 Jahre alt und Fullstack-Entwickler mit Schwerpunkt auf Frontend-Entwicklung. Meine Leidenschaft liegt der Entwicklung moderner, benutzerfreundlicher und performanter Anwendungen.

Meine berufliche Laufbahn begann mit einer Ausbildung zum Fachinformatiker für Anwendungsentwicklung, die ich im Januar 2025 erfolgreich abgeschlossen habe. Während dieser Zeit konnte ich wertvolle Erfahrungen in der Entwicklung von Software und Webanwendungen sammeln. Dabei habe ich gelernt, wie wichtig gute Zusammenarbeit und kontinuierliches Lernen sind. Ich arbeite gerne mit anderen zusammen, bin stets lernbereit und gehe flexibel auf neue Situationen ein.

Ich habe mit verschiedenen Programmiersprachen und Technologien gearbeitet, darunter JavaScript, React, Vue.js, Java, Spring Boot, HTML, CSS, PostgreSQL und Docker. Besonders begeistert mich die Arbeit mit JavaScript und React. Dabei lege ich großen Wert auf sauberen Code, hohe Performance und Benutzerfreundlichkeit.

Neben der Softwareentwicklung mache ich in meiner Freizeit Kung Fu und Fitness, spiele gerne Schach, reise sehr gerne und programmiere kleinere Projekte, die ich im Projektbereich verlinkt habe – dort finden sich einige meiner spannendsten Arbeiten.

Diese Website dient als Portfolio, um meine bisherigen Projekte zu präsentieren und einen Einblick in meine Arbeit zu geben.`,
      email: "elyas.jabari@hotmail.com",
      phone: "0152 193 422 52",
      github: "https://github.com/ElyasJabari",
      linkedin: "https://www.linkedin.com/in/elyas-jabari-3b4749342/",
    };
  },
  mounted() {
    /*For Animation*/
    const section = this.$refs.aboutSection;
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            section.classList.add("fade-in-visible");
          } else {
            section.classList.remove("fade-in-visible");
          }
        });
      },
      { threshold: 0.1 }
    );

    observer.observe(section);
  },
};
</script>

